@mixin OnDesktop {
  @media (min-width: 1024px) {
    @content;
  }
}

.page {
  font-family: Poppins, Arial, Helvetica, sans-serif;

  &__body {
    margin: 0;
  }
}

.container {
  // margin: 0 auto;
   max-width: 1020px;
   padding: 0 23px;
   margin: 0 auto;

    @include OnDesktop {
      padding: 0 40px;
   }
}

.icon {
  display: block;
  width: 18px;

  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;

}

.header {

  background-image: url("../images/photos/mainPhone.png");
  background-size: cover;
  background-position: center;

  @include OnDesktop {
    background-image: url("../images/photos/mainDesktop.png");
  }

  @media (min-width: 2000px) {
    background-image: url("../images/photos/mainBig.png");
  }

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    box-sizing: border-box;
    height: 100vh;
    padding: 29px 0 88px;
  }

  &__nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__right {
    display: flex;
    align-items: center;
    gap: 30px;
  }

  &__menu-opener {
    height: 12px;

    @include OnDesktop {
      width: 24px;
      height: 16px;
    }
    background-image: url('../images/menu.svg');

    &:hover {
    background-image: url('../images/menuHover.svg');

    }
  }

  &__call {
    height: 18px;

    @include OnDesktop {
      width: 24px;
      height: 24px;
    }

    background-image: url("../images/phone.svg");

    &::before {
      content: "+1 234 555-55-55";
      opacity: 1;
    }
  }

  &__title {
  font-weight: 700;
  font-size: 36px;
  line-height: 100%;
  text-align: center;

  @include OnDesktop {
    font-size: 80px;
  }

  letter-spacing: -1.5px;

  color: #fff;
  }

}

.logo {
  &__image {
    height: 38px;
    width: 130px;

    @include OnDesktop {
      width: 187px;
      height: 56px;
    }
  }
}
